import { Component, OnInit } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'
import {
  IonContent,
  IonHeader,
  IonTitle,
  IonToolbar,
  IonList,
  IonItem,
  IonLabel,
  IonText,
  IonIcon,
  IonBadge,
  IonRow,
  IonCol,
  IonProgressBar,
  IonRefresher,
  IonSkeletonText,
} from '@ionic/angular/standalone'
import { HeaderComponent } from 'src/app/components/header/header.component'
import { RentingService } from 'src/app/services/api-wrappers/renting.service'
import { bicycleOutline, leafOutline, flameOutline } from 'ionicons/icons'
import { addIcons } from 'ionicons'
import { co2savedPipe } from 'src/app/pipes/co2saved.pipe'
import { TranslateModule } from '@ngx-translate/core'
import { TripItemComponent } from '../../../shared/components/trip-item/trip-item.component'
import { TripsDringComponent } from 'src/app/components/dring/trips-dring/trips-dring.component'

export type TripsState =
  | {
      state: 'loading'
    }
  | {
      state: 'loaded'
      todayTrips: any[]
      yesterdayTrips: any[]
      olderTrips: any[]
      hasMoreTrips: boolean
    }
  | {
      state: 'noData'
    }

@Component({
  selector: 'app-trips',
  templateUrl: './trips.page.html',
  styleUrls: ['./trips.page.scss'],
  imports: [
    IonContent,
    IonList,
    IonItem,
    IonRow,
    TripsDringComponent,
    IonCol,
    IonLabel,
    IonBadge,
    IonText,
    IonProgressBar,
    IonRefresher,
    CommonModule,
    FormsModule,
    HeaderComponent,
    TranslateModule,
    TripItemComponent,
    IonSkeletonText,
  ],
})
export class TripsPage implements OnInit {
  tripsState: TripsState = { state: 'loading' }
  skeletonCount: number = 2

  constructor(private rentingService: RentingService) {
    addIcons({ leafOutline, flameOutline, bicycleOutline })
  }

  ngOnInit() {
    this.refresh()
  }

  handleRefresh(event: any) {
    this.refresh().then(() => {
      event.target.complete()
    })
  }

  refresh(): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      this.tripsState = { state: 'loading' }
      this.rentingService.getUserTrips().subscribe((trips) => {
        if (trips.data!.length === 0) {
          this.tripsState = { state: 'noData' }
          resolve()
          return
        }

        const now = Math.floor(Date.now() / 1000)
        const todayStart = now - (now % 86400)
        const yesterdayStart = todayStart - 86400

        const newState: TripsState = {
          state: 'loaded',
          hasMoreTrips: trips.has_more!,
          todayTrips: [],
          yesterdayTrips: [],
          olderTrips: [],
        }

        trips.data!.forEach((trip) => {
          if (trip.created! >= todayStart) {
            newState.todayTrips.push(trip)
          } else if (trip.created! >= yesterdayStart) {
            newState.yesterdayTrips.push(trip)
          } else {
            newState.olderTrips.push(trip)
          }
        })

        this.tripsState = newState
        resolve()
      })
    })
  }
}
