import { Component, Input } from '@angular/core'
import { IonicModule } from '@ionic/angular'
import { CommonModule } from '@angular/common'
import { co2savedPipe } from 'src/app/pipes/co2saved.pipe'
import { TranslateModule } from '@ngx-translate/core'
import { RentingService } from 'src/app/services/api-wrappers/renting.service'
import { addIcons } from 'ionicons'
import { lockOpen } from 'ionicons/icons'

@Component({
    selector: 'app-trip-item',
    imports: [IonicModule, CommonModule, co2savedPipe, TranslateModule],
    templateUrl: './trip-item.component.html'
})
export class TripItemComponent {
  @Input() trip: any
  constructor(public rentingService: RentingService) {
    addIcons({
      lockOpen,
    })
  }

  hasActiveTripOverThreeHours(): boolean {
    return this.rentingService.hasActiveTripOverThreeHours(this.trip)
  }

  hasActiveTripOver24Hours(): boolean {
    return this.rentingService.hasActiveTripOver24Hours(this.trip)
  }
}
