import { Pipe, PipeTransform } from '@angular/core'

@Pipe({
  name: 'co2saved',
  standalone: true,
})
export class co2savedPipe implements PipeTransform {
  transform(co2: number, distance: number, ...args: unknown[]): unknown {
    if (co2 > 0) {
      return Math.trunc((distance / 1000.0) * 220 - co2)
    } else {
      return co2
    }
  }
}
